















.dokan-dashboard .widgets-wrapper {
  display: block;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
}
.dokan-dashboard .widgets-wrapper .left-side,
.dokan-dashboard .widgets-wrapper .right-side {
  float: left;
  width: 48%;
}
.dokan-dashboard .widgets-wrapper .left-side {
  margin-right: 3%;
}
.dokan-dashboard .dokan-postbox .loading {
  display: block;
  width: 100%;
  margin: 15px auto;
  text-align: center;
}
.dokan-dashboard .subscribe-box {
  margin: 20px -12px -11px -12px;
  padding: 0 15px 15px;
  background: #fafafa;
  border-top: 1px solid #efefef;
  position: relative;
}
.dokan-dashboard .subscribe-box h3 {
  margin: 10px 0;
}
.dokan-dashboard .subscribe-box p {
  margin-bottom: 10px !important;
}
.dokan-dashboard .subscribe-box .thank-you {
  background: #4fa72b;
  margin-top: 10px;
  padding: 15px;
  border-radius: 3px;
  color: #fff;
}
.dokan-dashboard .subscribe-box .form-wrap {
  display: flex;
}
.dokan-dashboard .subscribe-box .form-wrap input[type="email"] {
  width: 100%;
  padding: 3px 0 3px 6px;
  margin: 0px -1px 0 0;
}
.dokan-dashboard .subscribe-box .form-wrap button.button {
  box-shadow: none;
  background: #FF5722;
  color: #fff;
  border-color: #FF5722;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.dokan-dashboard .subscribe-box .form-wrap button.button:hover {
  background: #ff6a3c;
}
.dokan-dashboard .subscribe-box .loading {
  position: absolute;
  height: 100%;
  margin: 0 0 0 -15px;
  background: rgba(0, 0, 0, 0.2);
}
.dokan-dashboard .subscribe-box .loading .dokan-loader {
  margin-top: 30px;
}

.withdraw-requests .dokan-modal .modal-body {
  min-height: 130px;
}
.withdraw-requests .dokan-modal .modal-body textarea {
  width: 100%;
}
.withdraw-requests .image {
  width: 10%;
}
.withdraw-requests .seller {
  width: 20%;
}
.withdraw-requests td.seller img {
  float: left;
  margin-right: 10px;
  margin-top: 1px;
  width: 24px;
  height: auto;
}
.withdraw-requests td.seller strong {
  display: block;
  margin-bottom: .2em;
  font-size: 14px;
}
.withdraw-requests td.actions,
.withdraw-requests th.actions {
  width: 120px;
}
.withdraw-requests td.status span {
  line-height: 2.5em;
  padding: 5px 8px;
  border-radius: 4px;
}
.withdraw-requests td.status .approved {
  background: #c6e1c6;
  color: #5b841b;
}
.withdraw-requests td.status .pending {
  background: #f8dda7;
  color: #94660c;
}
.withdraw-requests td.status .cancelled {
  background: #eba3a3;
  color: #761919;
}

.dokan-pro-features .pro-feature-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;
}
.dokan-pro-features .pro-feature {
  min-width: 48%;
  max-width: 48%;
  margin-bottom: 20px;
  border: 1px solid #E6E6E6;
  background: #fff;
}
@media screen and (max-width: 782px) {
.dokan-pro-features .pro-feature {
    min-width: 100%;
    max-width: 100%;
}
}
.dokan-pro-features .pro-feature:nth-child(2n) {
  margin-right: 0px;
}
.dokan-pro-features .pro-feature-thumb img {
  width: 100%;
  height: auto;
}
.dokan-pro-features .pro-detail {
  padding: 10px 20px;
  min-height: 100px;
  background: #fff;
}
.dokan-pro-features .pro-detail h3.title {
  margin: 5px 0 10px;
  padding: 0;
}
.dokan-pro-features .pro-detail h3.title a {
  text-decoration: none;
  color: #111;
}
.dokan-pro-features .pro-links {
  padding: 10px;
  background: #F5F5F5;
  border-top: 1px solid #E6E6E6;
}
.dokan-pro-features a.button.disabled {
  background: #eee;
}

.dokan-help-page .section-wrapper {
  margin-top: 15px;
}
.dokan-help-page .section-wrapper .dokan-postbox {
  width: calc(33% - 2em);
  margin: 0 2% 15px 0;
  float: left;
}
.dokan-help-page .section-wrapper .dokan-postbox:nth-child(3n+1) {
  clear: both;
}
.dokan-help-page .section-wrapper .dokan-postbox .dashicons {
  color: #ccc;
}
.dokan-help-page .section-wrapper .dokan-postbox a {
  text-decoration: none;
}
.dokan-help-page .section-wrapper .dokan-postbox .inside,
.dokan-help-page .section-wrapper .dokan-postbox ul {
  margin-bottom: 0;
}
.dokan-help-page .loading {
  width: 100%;
  text-align: center;
  margin-top: 100px;
}

.dokan-settings-wrap {
  position: relative;
  display: flex;
  border: 1px solid #c8d7e1;
}
.dokan-settings-wrap .loading {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
}
.dokan-settings-wrap .loading .dokan-loader {
  top: 40%;
  left: 45%;
}
.dokan-settings-wrap .dashicons {
  padding-top: 2px;
  margin-right: 5px;
}
.dokan-settings-wrap .dashicons.dashicons-admin-generic {
  color: #6c75ff;
}
.dokan-settings-wrap .dashicons.dashicons-cart {
  color: #00aeff;
}
.dokan-settings-wrap .dashicons.dashicons-money {
  color: #d35400;
}
.dokan-settings-wrap .dashicons.dashicons-admin-page {
  color: #8e44ad;
}
.dokan-settings-wrap .dashicons.dashicons-admin-appearance {
  color: #3498db;
}
.dokan-settings-wrap .dashicons.dashicons-networking {
  color: #1abc9c;
}
.dokan-settings-wrap h2.nav-tab-wrapper {
  flex: 1;
  border-bottom: none;
  padding: 0;
  background: #f1f1f1;
  border-right: 1px solid #c8d7e1;
}
.dokan-settings-wrap h2.nav-tab-wrapper a {
  float: none;
  display: block;
  margin: 0;
  border: none;
  padding: 13px 13px;
  background: #f1f1f1;
  font-weight: 500;
  border-bottom: 1px solid #c8d7e1;
  transition-property: none;
  transition: none;
}
.dokan-settings-wrap h2.nav-tab-wrapper a.nav-tab-active {
  background: #fff !important;
  border-right: 1px solid #c8d7e1;
  width: 99%;
  color: #2e4453;
  transition: none;
  transition-property: none;
}
.dokan-settings-wrap .metabox-holder {
  flex: 3;
  padding-left: 3%;
  padding-right: 10px;
  background: #fff;
}
.dokan-settings-wrap .radio-image-container .radio-image {
  display: block;
  width: 50%;
  background: #fff;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  margin: 0 0 15px;
  position: relative;
  line-height: 0;
  border: 1px solid #ededed;
  padding: 4px;
}
.dokan-settings-wrap .radio-image-container .radio-image img {
  max-width: 100%;
  z-index: 1;
}
.dokan-settings-wrap .radio-image-container .radio-image .current-option-indicator {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #4CAF50;
  color: #fff;
  padding: 4px;
  z-index: 2;
  line-height: 1.4;
}
.dokan-settings-wrap .radio-image-container .radio-image .active-option {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  transition: opacity 0.4s ease;
}
.dokan-settings-wrap .radio-image-container .radio-image .active-option button {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -23px;
  margin-left: -58px;
}
.dokan-settings-wrap .radio-image-container .radio-image:hover .active-option {
  opacity: 1;
}
.dokan-settings-wrap .radio-image-container .radio-image.active .active-option {
  display: none;
}
.dokan-settings-wrap .radio-image-container .radio-image.not-active .current-option-indicator {
  display: none;
}
.dokan-settings-wrap .radio-image-container label {
  display: block;
  margin-bottom: 5px;
}
.dokan-settings-wrap .radio-image-container label > input[type='radio'] {
  visibility: hidden;
  /* Makes input not-clickable */
  position: absolute;
  /* Remove input from document flow */
}
.dokan-settings-wrap .radio-image-container label > img {
  max-width: 100%;
}

